.nav--closed .nav__toggle {
  position: absolute;
  top: 0;
  right: 0;
  width: 50px;
  height: 50px;
  background: #0ad9c6;
  cursor: pointer;
  border: none; }
  .nav--closed .nav__toggle:hover, .nav--closed .nav__toggle:focus {
    background: #8bebd3; }
  .nav--closed .nav__toggle:active {
    background: #085850; }
  .nav--closed .nav__toggle::before {
    position: absolute;
    content: "";
    top: 50%;
    left: 50%;
    width: 23px;
    height: 1px;
    background-color: #1f1f1f;
    box-shadow: 0 8px 0 0 #1f1f1f, 0 -8px 0 0 #1f1f1f;
    transform: translateX(-50%); }
  .nav--closed .nav__toggle:hover::before, .nav--closed .nav__toggle:focus::before {
    opacity: 0.6; }
  .nav--closed .nav__toggle:active::before {
    opacity: 0.3; }

.nav--opened .nav__toggle {
  position: absolute;
  top: 15px;
  right: 3%;
  width: 23px;
  height: 23px;
  background: transparent;
  cursor: pointer;
  border: none; }
  .nav--opened .nav__toggle::before, .nav--opened .nav__toggle::after {
    position: absolute;
    content: "";
    top: 45%;
    left: 0;
    transform: translateY(-50%) rotate(45deg);
    width: 35px;
    height: 2px;
    background-color: rgba(20, 21, 23, 0.2); }
  .nav--opened .nav__toggle::after {
    transform: translateY(-50%) rotate(-45deg); }
  .nav--opened .nav__toggle:hover::before, .nav--opened .nav__toggle:focus::before, .nav--opened .nav__toggle:hover::after, .nav--opened .nav__toggle:focus::after {
    opacity: 0.6; }
  .nav--opened .nav__toggle:active::before, .nav--opened .nav__toggle:active::after {
    opacity: 0.3; }

.nav__toggle {
  display: none; }
  @media screen and (max-width: 1023px) {
    .nav__toggle {
      display: block; } }

.nav--no-js .nav__toggle {
  display: none; }

